import { ApiConfig, ApiSourceConfig } from '../types';
import {XodSourceMessageType} from "@xip-online-data/types";
import { KafkaSourceInterface } from '@xip-online-data/kafka-base-service';
import { v4 as uuidv4 } from 'uuid';
import {generateCollectionName, generateKafkaTopic} from "../helper.functions";
import { Logger } from '@transai/logger';


const getMetadataFromObject = (
  obj: object,
  metadata: {  [key: string]: string; }
): object => {
  try {
    return Object.keys(metadata).reduce((acc, key) => {
      const requiredVal = metadata[key];
      // @ts-ignore
      if (obj[requiredVal]) {
        // @ts-ignore
        acc[key] = obj[requiredVal];
      }
      return acc;
    }, {});
  } catch (e) {
    Logger.getInstance().debug('Error while getting metadata from object', e, obj, metadata);
    return {};
  }
}

export class KafkaService {
  constructor(
    private readonly kafkaSourceService: KafkaSourceInterface,
  ) {}

  public async sendDocuments(
    records: Array<any>,
    config: ApiSourceConfig,
    apiConfig: ApiConfig,
    metadata: object = {},
  ): Promise<boolean> {
    const kafkaPayload: Array<XodSourceMessageType> = records.map((record) => {
      const additionalMetadata = getMetadataFromObject(record, apiConfig.metadata ?? {});
      return {
        type: 'SOURCE' as const,
        eventId: uuidv4(),
        eventType: 'event.source-sink.kafka',
        created: Date.now(),
        ttl: 3600000, // 1 month
        tenantIdentifier: config.tenantIdentifier,
        payload: {
          keyField: apiConfig.keyField,
          incrementalField: apiConfig.incrementalField,
          collection: generateCollectionName(config, apiConfig),
          body: record,
          metadata: {
            ...metadata,
            ...additionalMetadata
          }
        }
      }
    });

    const topic = generateKafkaTopic(config);

    return this.kafkaSourceService.send(kafkaPayload, topic);
  }


  public async sendMetric(
    records: Array<any>,
    config: ApiSourceConfig,
    apiConfig: ApiConfig,
    metadata: object = {},
  ): Promise<boolean> {
    const kafkaPayload: Array<XodSourceMessageType> = records.map((body) => {
      const additionalMetadata = getMetadataFromObject(body, apiConfig.metadata ?? {});
      return {
        type: 'SOURCE',
        eventId: uuidv4(),
        eventType: 'event.metric',
        created: Date.now(),
        ttl: -1,
        tenantIdentifier: config.tenantIdentifier,
        payload: {
          body,
          metadata: {
            ...metadata,
            ...additionalMetadata
          }
        }
      }
    });

    const topic = generateKafkaTopic(config);
    return await this.kafkaSourceService.send(kafkaPayload, topic);
  }
}
